.game-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: #1e2a3a;
  color: white;
}

.game-header {
  width: 100%;
  background-color: #2c3e50;
  color: white;
  text-align: center;
  padding: 15px;
  margin-bottom: 20px;
}

.back-button {
  display: inline-block;
  margin: 10px;
  padding: 8px 16px;
  background-color: #3498db;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.back-button:hover {
  background-color: #2980b9;
}

.game-container {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
} 